import React from "react";
import { Button, Form, Input } from "antd";
import { LockOutlined, UserOutlined } from "@ant-design/icons";
import { RuleObject } from "antd/lib/form";
import { connect } from "react-redux";
import "./css/index.less";
import logo from "./imgs/logo.png";
import { reducersType } from "../../redux/reducers";
import {
  createDemo1Action,
  createDemo2Action,
} from "../../redux/actions_creators/test_action";
const { Item } = Form;

interface LoginProps {
  test: reducersType["test"];
  demo1: typeof createDemo1Action;
  demo2: typeof createDemo2Action;
}

const FormLogin: React.FC<LoginProps> = (props: LoginProps) => {
  const onFinish = (values: any) => {
    // alert("");
    console.log("");
    console.log("Success:", values);
    props.demo2("0719");
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  const pwdValidator = (
    rule: RuleObject,
    value: string
  ): Promise<void | any> | void => {
    // console.log("pwd", rule);
    // console.log("pwdva", value);
    if (!value) {
      return Promise.reject("");
    } else if (value.length > 12) {
      return Promise.reject("12");
    } else if (value.length < 4) {
      return Promise.reject("4");
    } else if (!/^\w+$/.test(value)) {
      return Promise.reject("");
    }
    return Promise.resolve();
  };

  return (
    <Form
      name="basic"
      labelCol={{ span: 8 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      autoComplete="off"
      className="Form"
    >
      <Item
        label=""
        name="username"
        rules={[
          { required: true, message: "" },
          { min: 4, message: "4" },
          { max: 12, message: "12" },
          { pattern: /^\w+$/, message: "" },
        ]}
      >
        <Input prefix={<UserOutlined />} />
      </Item>
      <Item
        label=""
        name="password"
        // rules={[
        //   { required: true, message: "" },
        //   { min: 4, message: "4" },
        //   { max: 12, message: "12" },
        //   { pattern: /^\w+$/, message: "" },
        // ]}
        rules={[{ validator: pwdValidator }]}
      >
        <Input.Password prefix={<LockOutlined />} />
      </Item>

      <Item wrapperCol={{ offset: 0, span: 24 }}>
        <Button type="primary" htmlType="submit" className="Item_Login_Button">
          Submit
        </Button>
      </Item>
    </Form>
  );
};

function Login(props: LoginProps) {
  // console.log(typeof props.test);
  return (
    <div className="login">
      <header>
        <img src={logo} alt="" />
        <h1>{(props as any).test}</h1>
      </header>
      <section>
        <div className="wrapper">
          <h1></h1>
          <FormLogin
            test={props.test}
            demo1={props.demo1}
            demo2={props.demo2}
          />
        </div>
      </section>
    </div>
  );
}

export default connect(
  (state: reducersType) => ({
    test: state.test,
  }),
  {
    demo1: createDemo1Action,
    demo2: createDemo2Action,
  }
)(Login);
